        -ms-transform: translateX(15%);
            transform: translateX(15%);
  }
}

@media (max-width: 850px) {
  .welcome-selfie {
    -webkit-transform: translateX(30%);
        -ms-transform: translateX(30%);
            transform: translateX(30%);
  }
}

@media (max-width: 600px) {
  .welcome-selfie {
    background-size: contain;
    -ms-flex: 1 1;
        flex: 1 1;
    max-height: none;
    max-width: none;
    min-height: 0;
    min-width: 0;
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
  }
}

.welcome-fade-overlay {
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#07070a));
  background: -webkit-linear-gradient(transparent, #07070a);
  background: -o-linear-gradient(transparent, #07070a);
  background: linear-gradient(transparent, #07070a);
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}



