.skill-icon {
  width: 180px;
  height: 180px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.5;
  margin: 0 100px;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 250ms;
       -o-transition-duration: 250ms;
          transition-duration: 250ms;
}

.skill-icon:hover {
  opacity: 1;
}

.skill-icon-text {
  font-weight: 500;
  letter-spacing: 5px;
  font-size: 2vw;
  opacity: 2;
  -webkit-transform: translateX(40px) translateY(140px);
      -ms-transform: translateX(40px) translateY(140px);
          transform: translateX(40px) translateY(140px);
}


