.navbar {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  background: rgba(255, 255, 255, 0);
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  left: 120px;
  right: 120px;
  top: 10px;
  z-index: 10000;
}

@media (max-width: 1000px) {
  .navbar {
    left: 50px;
    right: 50px;
  }
}

@media (max-width: 600px) {
  .navbar {
    -ms-flex-direction: column;
        flex-direction: column;
    left: 0;
    right: 0;
  }
}

.navbar-logo {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 50px;
  height: 50px;
  margin: 0;
  left: 0;
}

.navbar-buttons {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  height: 70px;
  background: transparent;
}

@media (max-width: 600px) {
  .navbar-buttons {
    height: 50px;
  }
}


