.portfolio {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
}

.portfolio > .title {
  z-index: 11;
}

.portfolio > .content {
  position: relative;
  z-index: 11;
}

.projectMenu {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  line-height: 40px;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
}

.projectMenuItem {
  -o-text-overflow: clip;
     text-overflow: clip;
  text-transform: uppercase;
  font-weight: 900;
  height: 9vw;
  line-height: 9vw;
  font-size: 6vw;
  letter-spacing: 1px;
  border-bottom: 5px solid rgba(255, 255, 255, 0);
  -webkit-transition-property: border-color;
  -o-transition-property: border-color;
  transition-property: border-color;
  -webkit-transition-duration: 200ms;
       -o-transition-duration: 200ms;
          transition-duration: 200ms;
}
.projectMenuItem:hover {
  border-color: #fff;
}

.projectMenuItemDivider {
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  height: 9vw;
  padding: 0 2vw;
  width: 15vw;
}

.projectMenuItemDivider-white-space {
  -ms-flex: 1 1;
      flex: 1 1;
  min-height: 2px;
  background: #fff;
}

.questBackground,
.railsightBackground,
.richBackground,
.proximityBackground,
.uproarBackground,
.questBackground-visible,
.railsightBackground-visible,
.richBackground-visible,
.proximityBackground-visible,
.uproarBackground-visible {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: opacity 400ms, -webkit-transform 400ms;
  transition: opacity 400ms, -webkit-transform 400ms;
  -o-transition: opacity 400ms, transform 400ms;
  transition: opacity 400ms, transform 400ms;
  transition: opacity 400ms, transform 400ms, -webkit-transform 400ms;
  z-index: 10;
  opacity: 0;
}

.questBackground-visible,
.railsightBackground-visible,
.richBackground-visible,
.proximityBackground-visible,
.uproarBackground-visible {
  opacity: .8;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.scroll-to-portfolio {
  z-index: 4;
}

@-webkit-keyframes backgroundChange {
  0% {
    background-size: 120%;
    -webkit-background-size: 120%;
  }
  100% {
    background-size: 100% contain;
    -webkit-background-size: 100%;
  }
}

@keyframes backgroundChange {
  0% {
    background-size: 120%;
    -webkit-background-size: 120%;
  }
  100% {
    background-size: 100% contain;
    -webkit-background-size: 100%;
  }
}



